import styled from 'styled-components';
import { motion } from 'framer-motion';
import { useState } from 'react';
import { useInView } from 'react-intersection-observer';
import Images from '../Img/IndexImg';

const BodyWrapper = styled.div`
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    top: 0;
    font: 
`;

const MainBox = styled.div`
    width: 100%;
    height: 600px;
    overflow: hidden;

        @media (min-width: 1000px) {
          
        }
`;

const MainPhotoBox = styled.div`
    width: 100%;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
`;

const MainPhoto = styled.img`
    transform: translateX(-70px);
        @media (min-width: 800px) {
            transform: translateX(0);;
        }
        @media (min-width: 1050px) {
            width: 100%;
            
        }
        @media (min-width: 1300px) {
            width: 100%;
            transform: translateY(-70px);
        }
`;

const MainTitle = styled(motion.div)`
    display: flex;
    flex-direction: column;
    font-weight: 800;
    text-shadow: 3px 5px 3px rgba(0, 0, 0, 20%);
    
`;
const MainTitleUpper = styled(motion.div)`
    
`;
const UpperText = styled(motion.p)`
    color: rgba(248, 249, 251, 100%);
    font-size: 48px;
    text-align: center;
        @media (min-width: 500px) {
            font-size: 68px;
        }
`;
const MainTitleDown = styled(motion.div)``;
const DownText = styled(motion.p)`
    color: rgba(254, 131, 1, 100%);
    font-size: 63px;
    text-align: right;
    transform: translateX(50px);
    @media (min-width: 500px) {
        font-size: 78px;
    }
`;

const InfoWrapper = styled.div`
    margin-top: 100px;
    margin-bottom: 100px;
`;

const InfoBoxWrapper = styled.div`
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-shadow: 3px 5px 3px rgba(0, 0, 0, 20%);
    font-weight: 700;
    font-size: 58px;
        @media (min-width: 420px) {
            font-size: 58px;
        }
        @media (min-width: 840px) {
            flex-direction: row;
            font-size: 68px;
        }
`;

const InfoPhotoFrame = styled.div`
    width: 100%;
    margin-top: 80px;
        @media (min-width: 420px) {
            width: 420px;
        }
`;

const InfoPhotoLeft = styled.img`
    width: 100%;
    transform: translateX(-30px);
        @media (min-width: 420px) {
            width: 420px;
        }
`;

const InfoPhotoRight = styled.img`
    width: 100%;
    transform: translateX(30px);
        @media (min-width: 420px) {
            width: 420px;
        }
`;

const InfoBox1 = styled(motion.div)`
    width: 100%;
    margin-right: 15px;
    
`;

const InfoBox2 = styled(motion.div)`
    width: 100%;
    margin-left: 15px;
    
`;

const InfoBox3 = styled(InfoBox1)`
`;

const InfoBox4 = styled(InfoBox2)`
`;

const InfoBox5 = styled(InfoBox1)`
`;

const TextBox = styled(motion.div)`
`;

const InfoTextUpperRight = styled(motion.p)`
    color: rgba(248, 249, 251, 100%);
    text-align: right;
`;

const InfoTextUpperLeft = styled(InfoTextUpperRight)`
    text-align: left;
        @media (min-width: 840px) {
            text-align: right;
        }
`;

const InfoTextDownRight = styled(motion.p)`
    color: rgba(254, 131, 1, 100%);
    text-align: right;
`;

const InfoTextDownLeft = styled(InfoTextDownRight)`
    text-align: left;
        @media (min-width: 840px) {
            text-align: right;
        }
`;

const titleVariants = {
    hidden: {
        opacity: 0,
        y: 0
    },
    visible: {
        opacity: 1,
        y: -50,
        transition: {
            duration: 2,
        }
    }
}

const textBoxVariants = {
    hidden: {
        opacity: 0,
        y: 0
    },
    visible: {
        opacity: 1,
        y: -45,
        transition: {
            duration: 1.2,
        }
    }
}

function Restaurant() {
    const {ref: titleRef, inView: titleInView} = useInView({triggerOnce: true});
    const {ref: box1Ref, inView: box1InView} = useInView({triggerOnce: true});
    const {ref: box2Ref, inView: box2InView} = useInView({triggerOnce: true});
    const {ref: box3Ref, inView: box3InView} = useInView({triggerOnce: true});
    const {ref: box4Ref, inView: box4InView} = useInView({triggerOnce: true});
    const {ref: box5Ref, inView: box5InView} = useInView({triggerOnce: true});

    return (
        <BodyWrapper>
           <MainBox>
                <MainPhotoBox>
                    <MainPhoto src={Images.resMain} />
                </MainPhotoBox>
           </MainBox>
           <MainTitle ref={titleRef}>
                {titleInView ? 
                <TextBox
                variants = {titleVariants}
                initial = "hidden"
                animate = "visible"
                >
                    <MainTitleUpper>
                        <UpperText>restaurant</UpperText>
                    </MainTitleUpper>
                    <MainTitleDown>
                        <DownText>bada</DownText>
                    </MainTitleDown>
                </TextBox> : null}
           </MainTitle>

           <InfoWrapper>
                <InfoBoxWrapper>
                        <InfoPhotoFrame>
                            <InfoPhotoLeft src={Images.resChef} />
                        </InfoPhotoFrame>
                        <InfoBox1 
                            ref={box1Ref} 
                        >
                            {box1InView? <TextBox
                             variants={textBoxVariants}
                             initial="hidden"
                             animate = "visible">
                            <InfoTextUpperRight>worldclass</InfoTextUpperRight>
                            <InfoTextDownRight>chefs</InfoTextDownRight></TextBox> : null}
                        </InfoBox1>
                </InfoBoxWrapper>

                <InfoBoxWrapper>
                        <InfoPhotoFrame>
                            <InfoPhotoRight src={Images.resSeafoods} />
                        </InfoPhotoFrame>
                        <InfoBox2 ref={box2Ref}>
                            {box2InView? <TextBox
                                variants={textBoxVariants}
                                initial="hidden"
                                animate="visible">
                            <InfoTextUpperLeft>fresh</InfoTextUpperLeft>
                            <InfoTextDownLeft>seafood</InfoTextDownLeft></TextBox> : null}
                        </InfoBox2>
                </ InfoBoxWrapper>

                <InfoBoxWrapper>
                        <InfoPhotoFrame>
                            <InfoPhotoLeft src={Images.resIngredients} />
                        </InfoPhotoFrame>
                        <InfoBox3 ref={box3Ref}>
                            {box3InView ? <TextBox
                            variants= {textBoxVariants}
                            initial = "hidden"
                            animate = "visible"
                            >
                                <InfoTextUpperRight>local</InfoTextUpperRight>
                                <InfoTextDownRight>ingredients</InfoTextDownRight>
                            </TextBox> : null}
                            
                        </InfoBox3>
                </ InfoBoxWrapper>

                <InfoBoxWrapper>
                        <InfoPhotoFrame>
                            <InfoPhotoRight src={Images.resRestaurant} />
                        </InfoPhotoFrame>
                        <InfoBox4 ref={box4Ref}>
                            {box4InView ? 
                            <TextBox
                            variants= {textBoxVariants}
                            initial = "hidden"
                            animate = "visible"
                            >
                                <InfoTextUpperLeft>beautiful</InfoTextUpperLeft>
                                <InfoTextDownLeft>ocean</InfoTextDownLeft>
                            </TextBox> : null}
                        </InfoBox4>
                </ InfoBoxWrapper>

                <InfoBoxWrapper>
                        <InfoPhotoFrame>
                            <InfoPhotoLeft src={Images.resEvents} />
                        </InfoPhotoFrame>
                        <InfoBox5 ref={box5Ref}>
                            {box5InView ?
                             <TextBox
                             variants = {textBoxVariants}
                             initial = "hidden"
                             animate = "visible"
                             >
                                <InfoTextUpperRight>memorable</InfoTextUpperRight>
                                <InfoTextDownRight>events</InfoTextDownRight>
                            </TextBox> : null}
                        </InfoBox5>
                </InfoBoxWrapper>
           </InfoWrapper>
        </BodyWrapper>
    );
}

export default Restaurant;