import styled from 'styled-components';
import { motion, AnimatePresence } from 'framer-motion';
import { wrap } from 'popmotion';
import { useState } from 'react';
import { useInView } from 'react-intersection-observer';
import { Link } from 'react-router-dom';
import Images from '../Img/IndexImg';


const BodyWrapper = styled.div`
    top: 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
`;

const MainBox = styled.div`
    top:0;
    width: 100%;
    overflow: hidden;

        @media (min-width: 1000px) {
          
        }
`;

const MainPhotoBox = styled.div`
    top: 0;
    height: 250px;
    width: 100%;
    background-color: ;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
        @media (max-width: 340px) {
            height: 200px;
        }
        @media (min-width: 600px) {
            height: 400px;
        }
        @media (min-width: 1200px) {
            height: 500px;
        }
`;

const MainPhoto = styled.img`
    width: 100%;
    top: 0;
        @media (max-width: 340px) {
            height: 250px;
            width: auto;
        }

`;

const MainTitleBox = styled(motion.div)`
    color: rgba(255, 255, 255, 1);
    font-size: 22px;
    font-weight: 500;
    text-align: center;
    margin-bottom: 50px;
    transform: translateY(-40px);
    word-spacing: 2px;
        @media (min-width: 600px) {
            font-size: 34px;
            transform: translateY(-50px);
        }
        @media (min-width: 1200px) {
            font-size: 48px;
            transform: translateY(-70px);
        }
`;

const MainTitle = styled(motion.p)`

`;

const CategoryBox = styled.div`
    color: rgba(81, 108, 116, 1 );
    font-weight: 900;
    font-size: 38px;
    margin-top: 50px;
    width: 100%;
    padding-left: 30px;
        @media (min-width: 600px) {
            width: 600px;
        }
`;


const SlideBox = styled(motion.div)`
    margin-top: 30px;
    display: flex;
    justify-content: center;
    position: ;
`;

const RoomBoxRow = styled(motion.div)`
    
`;

const RoomBox = styled(motion.div)`
    width: 100%;
    height: 400px;
    overflow: hidden;
    background-color: ;
    position: ;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
        @media (min-width: 600px) {
            width: 600px;
            height: 470px;
        }
`;

const RoomImgBox = styled(motion.div)`
    width: 100%;
    height: 280px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
        @media (min-width: 600px) {
            height: 350px;
        }
`;

const RoomImg = styled(motion.img)`
    width: 100%;
    position: ;
`;

const RoomNameBox = styled(motion.div)`
    width: 100%;
    height: 120px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 10px;
    padding-right: 10px;
`;

const RoomName = styled(motion.p)`
    color: rgba(94, 93, 93, 1);
    font-weight: 700;
    font-size: 24px;
        @media (min-width: 600px) {
            font-size: 32px;
        }
`;

const LeftBtn = styled.button`
    border: none;
    background: none;
`;

const RightBtn = styled.button`
    border: none;
    background: none;
`;

const ButtonIcon = styled.svg`
    width: 30px;
    fill: rgba(255,255,255,0.9);
    path {
        stroke: #a8a69e;
        stroke-width: 7px;
    }
    &:hover {
        fill: rgba(126,165,177, 0.5);
    }
`;

const TasteBox = styled.div`
    width: 100%;
    margin-top: 100px;
    margin-bottom: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
        @media(min-width: 600px) {
            width: 600px;
        }
`;

const AromaBox = styled(TasteBox)`
    
`;

const CategoryTitle = styled.div`
    color: rgba(81, 108, 116, 1 );
    font-weight: 900;
    font-size: 38px;
    width: 100%;
    padding-left: 30px;
        
`;

const TasteImgBox = styled(motion.div)`
    width: 100%;
    height: 280px;
    background-color: red;
    margin-top: 20px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
        @media (min-width: 600px) {
            width: 600px;
            height: 350px;
        }
`;

const TasteImg = styled(motion.img)`
    width: 100%;
`;

const CafeImgBox = styled(TasteImgBox)`

`;

const CafeImg = styled(TasteImg)`

`;

const StyledLink = styled(Link)`
    color: rgba(94, 93, 93, 1);
    text-decoration: none;
    font-weight: 700;
    font-size: 20px;
    padding-left: 20px;
    width: 100%;
    margin-top: 20px;
    &:hover {
        color: rgba(254, 131, 1, 1),
    }
    `;

const IconInfo = styled.svg`
width: 24px;
margin-left: 10px;
fill: rgba(94, 93, 93, 1);
path {
    stroke: #a8a69e;
    stroke-width: 7px;
}
&:hover {
    fill: rgba(254, 131, 1, 1);
}
`;



const RoomImgVariants = {
    enter: {
        opacity: 0,
    },
    center: {
        opacity: 1,
    },
    exit: {
        opacity: 0,
        }
    }

// const swipeConfidenceThreshold = 10000;
// const swipePower = (offset: number, velocity: number) => {
//     return Math.abs(offset) * velocity;
// };


function Home() {
    const [index, setIndex] = useState(1);
    const next = () => setIndex(prev => (prev === 6 ? 1 : prev + 1));
    const back = () => setIndex(prev => (prev === 1 ? 6 : prev -1));

    return (
        <BodyWrapper>
            <MainBox>
                <MainPhotoBox>
                    <MainPhoto src={Images.hotelMain} />
                </MainPhotoBox>
                <MainTitleBox>
                    <MainTitle>find yourself at hotel bada</MainTitle>
                </MainTitleBox>
            </MainBox>

            <CategoryBox>Stay</CategoryBox>
            <SlideBox>
                    <RoomBox key={index}>
                        <AnimatePresence>
                            <RoomImgBox
                                
                                variants={RoomImgVariants}
                                initial = "enter"
                                animate = "center"
                                exit = "exit"
                                transition = {{
                                    x: {type: "tween"},
                                    opacity: { duration: 0.5 }
                                }}
                                // drag = "x"
                                // dragConstraints = {{ left: 0, right: 0 }}
                                // dragElastic = {1}
                                // onDragEnd = {(e, { offset, velocity }) => {
                                //     const swipe = swipePower(offset.x, velocity.x);

                                //     if (swipe < -swipeConfidenceThreshold) {
                                //         next();
                                //     } else if ( swipe > swipeConfidenceThreshold ) {
                                //         back();
                                //     }
                                // }}
                            >
                                { index === 1 && < RoomImg src={Images.hotelBada1} />}
                                { index === 2 && < RoomImg src={Images.hotelBada2} />}
                                { index === 3 && < RoomImg src={Images.hotelBada3} />}
                                { index === 4 && < RoomImg src={Images.hotelBada4} />}
                                { index === 5 && < RoomImg src={Images.hotelBada5} />}
                                { index === 6 && < RoomImg src={Images.hotelBada6} />}
                            </RoomImgBox>
                        </AnimatePresence>

                        <RoomNameBox key={index}>
                            <LeftBtn onClick={back}>
                                <ButtonIcon xmlns="http://www.w3.org/2000/svg" viewBox="0 0 384 512">
                                <path d="M41.4 233.4c-12.5 12.5-12.5 32.8 0 45.3l192 192c12.5 12.5 32.8 12.5 45.3 0s12.5-32.8 0-45.3L109.3 256 278.6 86.6c12.5-12.5 12.5-32.8 0-45.3s-32.8-12.5-45.3 0l-192 192z"/></ButtonIcon>
                            </LeftBtn>

                            { index === 1 && <RoomName>bada 1</RoomName>}
                            { index === 2 && <RoomName>bada 2</RoomName>}
                            { index === 3 && <RoomName>bada 3</RoomName>}
                            { index === 4 && <RoomName>bada 4</RoomName>}
                            { index === 5 && <RoomName>bada 5</RoomName>}
                            { index === 6 && <RoomName>bada 6</RoomName>}

                            <RightBtn onClick={next} >
                                <ButtonIcon xmlns="http://www.w3.org/2000/svg" viewBox="0 0 384 512">
                                <path d="M342.6 233.4c12.5 12.5 12.5 32.8 0 45.3l-192 192c-12.5 12.5-32.8 12.5-45.3 0s-12.5-32.8 0-45.3L274.7 256 105.4 86.6c-12.5-12.5-12.5-32.8 0-45.3s32.8-12.5 45.3 0l192 192z"/></ButtonIcon>
                            </RightBtn>
                        </RoomNameBox>
                    </RoomBox>
            </SlideBox>

            <TasteBox>
                <CategoryTitle>Taste</CategoryTitle>
                <Link to="/restaurant">
                    <TasteImgBox>
                        <TasteImg src={Images.hotelResInfo}></TasteImg>
                    </TasteImgBox>
                </Link>
                    <StyledLink to="/restaurant">
                        Restaurant bada 
                        <IconInfo xmlns="http://www.w3.org/2000/svg" 
                        viewBox="0 0 512 512">
                        <path d="M64 32C28.7 32 0 60.7 0 96s28.7 64 64 64h1c3.7 88.9 77 160 167 160h56V128H264 88.8 64c-17.7 0-32-14.3-32-32s14.3-32 32-32H464c8.8 0 16-7.2 16-16s-7.2-16-16-16H64zM224 456c0 13.3 10.7 24 24 24h72V407.8l-64.1-22.4c-12.5-4.4-26.2 2.2-30.6 14.7s2.2 26.2 14.7 30.6l4.5 1.6C233 433.9 224 443.9 224 456zm128 23.3c36.4-3.3 69.5-17.6 96.1-39.6l-86.5-34.6c-3 1.8-6.2 3.2-9.6 4.3v69.9zM472.6 415c24.6-30.3 39.4-68.9 39.4-111c0-12.3-1.3-24.3-3.7-35.9L382.8 355.1c.8 3.4 1.2 7 1.2 10.6c0 4.6-.7 9-1.9 13.1L472.6 415zM336 128H320V320h18.3c9.9 0 19.1 3.2 26.6 8.5l133.5-92.4C471.8 172.6 409.1 128 336 128zM216 192c0 13.3-10.7 24-24 24s-24-10.7-24-24s10.7-24 24-24s24 10.7 24 24z"/></IconInfo>

                    </StyledLink>

            </TasteBox>

            <AromaBox>
                <CategoryTitle>Aroma</CategoryTitle>
                <Link to="/cafe">
                    <CafeImgBox>
                        <CafeImg src={Images.hotelCafeInfo}></CafeImg>
                    </CafeImgBox>
                </Link>
                    <StyledLink to="/cafe">
                        Cafe bada 
                        <IconInfo xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512">
                        <path d="M88 0C74.7 0 64 10.7 64 24c0 38.9 23.4 59.4 39.1 73.1l1.1 1C120.5 112.3 128 119.9 128 136c0 13.3 10.7 24 24 24s24-10.7 24-24c0-38.9-23.4-59.4-39.1-73.1l-1.1-1C119.5 47.7 112 40.1 112 24c0-13.3-10.7-24-24-24zM32 192c-17.7 0-32 14.3-32 32V416c0 53 43 96 96 96H288c53 0 96-43 96-96h16c61.9 0 112-50.1 112-112s-50.1-112-112-112H352 32zm352 64h16c26.5 0 48 21.5 48 48s-21.5 48-48 48H384V256zM224 24c0-13.3-10.7-24-24-24s-24 10.7-24 24c0 38.9 23.4 59.4 39.1 73.1l1.1 1C232.5 112.3 240 119.9 240 136c0 13.3 10.7 24 24 24s24-10.7 24-24c0-38.9-23.4-59.4-39.1-73.1l-1.1-1C231.5 47.7 224 40.1 224 24z"/>
                        </IconInfo>
                    </StyledLink>

            </AromaBox>

        </BodyWrapper>
    )
}

export default Home;