import styled from 'styled-components';
import { motion, useAnimation } from 'framer-motion';
import {useEffect, useState, useRef} from 'react';
import { useInView } from 'react-intersection-observer';
import Images from '../Img/IndexImg';


const Main = styled.div`
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    top: 0;
`;

const SlideBox = styled(motion.div)`
    width: 100%;
    height: 600px;
    background-color: ;
    position: relative;
    top: 0;
    @media (min-width: 450px) {
        height: 600px;
    }
`;

const PhotoBox = styled(motion.div)`
    width: 100%;
    height: 600px;
    overflow: hidden;
        @media (min-width: 450px) {
            display: flex;
            justify-content: center;
            align-items: center;
            height: 600px;
        }
`;

const MainImg = styled(motion.img)`
    width: 100%;
`;

const LeftBtn = styled.button`
    position: absolute;
    width: ;
    top: 45%;
    left: 20px;
    border: none;
    background: none;
`;

const RightBtn = styled.button`
    position: absolute;
    width: ;
    top: 45%;
    right: 20px;
    border: none;
    background: none;
`;

const ButtonIcon = styled.svg`
    width: 30px;
    fill: rgba(255,255,255,0.9);
    path {
        stroke: #a8a69e;
        stroke-width: 7px;
    }
    &:hover {
        fill: rgba(255,255,255,1);
    }
`;

const MainMessageBox = styled(motion.div)`
    margin-top: 50px;
    margin-bottom: 50px;    
    width: 100%;
    color: rgba(0, 0, 0, 1);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
        @media (min-width: 450px) {
            margin-top: 60px;
        }

`;

const H1 = styled.h1`
    font-size: 48px;
    font-weight: 800;
        @media (min-width: 450px) {
            font-size: 58px;
        }

`;

const H2 = styled.h2`
    font-size: 24px;
    margin-top: 30px;
    font-weight: 400;
    color: rgba(126,165,177, 1);  
        @media (min-width: 450px) {
            font-size: 32px;
            margin-top: 20px;
        }  
`;

const AboutBox = styled.div`
    width: 100%;
    height: ;
    overflow: hidden;
    margin-top: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
        @media (min-width: 1000px) {
            flex-direction: row;
            justify-content: space-evenly;
        }
`;

const AboutImg = styled.img`
    width: 100%;

        @media (min-width: 600px) {
            width: 600px;
        }
`;

const AboutInfo = styled(motion.div)`
    width: 70%;
    height: 170px;
    background-color: rgba(217, 217, 217, 70%);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
        @media (min-width: 500px) {
            width: 400px;
            height: 200px;
        }
        @media (min-width: 1000px) {
            height: 360px;
            background-color: rgba(217, 217, 217, 0);
        }
`;

const InfoH3 = styled(motion.h3)`
    font-size: 35px;
    font-weight: 800;
    margin-bottom: 15px;
        @media (min-width: 600px) {
            font-size: 48px;
    }
`;

const InfoH4 = styled(motion.h4)`
    font-size: 18px;
    font-weight: 700;
    color: rgba(126,165,177, 1);
`;

const aboutInfoVariants ={
    hidden: {
        opacity: 0,
        y: 0
    },
    visible: {
        opacity: 1,
        y: -50,
        transition: {
            duration: 2,
        }
    },
}

function Cafe() {
    const { ref: viewRef, inView: viewInView } = useInView({triggerOnce: true});
    const { ref: aromaRef, inView: aromaInView } = useInView({triggerOnce: true});
    const { ref: savourRef, inView: savourInView } = useInView({triggerOnce: true});

    const [visible, setVisible] = useState(1);
    const next = () => setVisible(prev => (prev === 4 ? 1 : prev + 1));
    const back = () => setVisible(prev => (prev === 1 ? 4 : prev -1));

    return (
        <Main>
            <SlideBox>
                <PhotoBox
                    key={visible}
                >
                    {visible === 1 && <><MainImg src={Images.cafeMainImg_1} /></>}
                    {visible === 2 && <><MainImg src={Images.cafeMainImg_2} /></>}
                    {visible === 3 && <><MainImg src={Images.cafeMainImg_3} /></>}
                    {visible === 4 && <><MainImg src={Images.cafeMainImg_4} /></>}
                </PhotoBox>
                <LeftBtn onClick={back}>
                    <ButtonIcon xmlns="http://www.w3.org/2000/svg" viewBox="0 0 384 512">
                    <path d="M41.4 233.4c-12.5 12.5-12.5 32.8 0 45.3l192 192c12.5 12.5 32.8 12.5 45.3 0s12.5-32.8 0-45.3L109.3 256 278.6 86.6c12.5-12.5 12.5-32.8 0-45.3s-32.8-12.5-45.3 0l-192 192z"/></ButtonIcon>
                </LeftBtn>
                <RightBtn onClick={next} >
                    <ButtonIcon xmlns="http://www.w3.org/2000/svg" viewBox="0 0 384 512">
                    <path d="M342.6 233.4c12.5 12.5 12.5 32.8 0 45.3l-192 192c-12.5 12.5-32.8 12.5-45.3 0s-12.5-32.8 0-45.3L274.7 256 105.4 86.6c-12.5-12.5-12.5-32.8 0-45.3s32.8-12.5 45.3 0l192 192z"/></ButtonIcon>
                </RightBtn>
            </SlideBox>
            
            <MainMessageBox>
                <H1>cafe bada</H1>
                <H2></H2>
            </MainMessageBox>

            <AboutBox ref={viewRef}>
                <AboutImg src={Images.cafeAboutImg_1} />
                    {viewInView ? <AboutInfo 
                    style={{y:-50}}
                    variants = {aboutInfoVariants}
                    initial = "hidden"
                    animate = "visible"
                    >
                        <InfoH3>view</InfoH3>
                        <InfoH4>beautiful Bedford Basin</InfoH4>
                    </AboutInfo> : null}
            </AboutBox>

            <AboutBox ref={aromaRef}>
                <AboutImg src={Images.cafeAboutImg_2} />
                {aromaInView ? 
                <AboutInfo 
                style={{y:-50}}
                variants = {aboutInfoVariants}
                initial = "hidden"
                animate = "visible"
                >
                    <InfoH3>aroma</InfoH3>
                    <InfoH4>top pour over coffee</InfoH4>
                </AboutInfo> : null}
            </AboutBox>
            <AboutBox ref={savourRef} >
                <AboutImg src={Images.cafeAboutImg_3} />
                {savourInView ? 
                <AboutInfo 
                style={{y:-50}}
                variants={aboutInfoVariants}
                initial="hidden"
                animate="visible"
                >
                    <InfoH3>savour</InfoH3>
                    <InfoH4>fresh local pastries</InfoH4>
                </AboutInfo> : null}
            </AboutBox>

        </Main>
    );
}

export default Cafe;